// Common list styles can be found in "./common.styles/layouts/lists"
invitation-ami {
  ion-header {
    @include header-styles(color($colors, header-alt, base), color($colors, header-alt, contrast));
  }

  ion-row ion-col.row-opaque {
      opacity: 0.6;
    }

  .DejaInvite {
    opacity: 0.6;
    text-decoration:line-through;
  }

	.list-mini-content
	{
		background-color: color($colors, background, base);

		.list-mini
		{
			@include layout-list-styles($theme-color-2, $white-d);

			ion-list {
				.list-item
				{
					.content-row
					{
						.item-title{
							overflow: hidden;
							text-overflow: ellipsis;
							white-space: nowrap;
						}

						&.one-line
						{
							@include align-items(center);

							.item-description
							{
								overflow: hidden;
								text-overflow: ellipsis;
								white-space: nowrap;
							}
						}
					}
				}
			}
		}
	}
}
